import request from '../../request'
//
export function carExpsList(data) {
    return request({
        url: '/carExps/page',
        method: 'post',
        data
    })
}
//
export function carExpsDelete(data) {
    return request({
        url: '/carExps/delete',
        method: 'post',
        data
    })
}
//
export function carExpsSearch(data) {
    return request({
        url: '/carExps/fuzzyQuery',
        method: 'post',
        data
    })
}

//
export function carExpsCount(data) {
    return request({
        url: '/carExps/count',
        method: 'post',
        data
    })
}