import request from '../../request'
//
export function oilCarList(data) {
    return request({
        url: '/refuel/page',
        method: 'post',
        data
    })
}
//
export function oilCarAdd(data) {
    return request({
        url: '/refuel/add',
        method: 'post',
        data
        // headers:{'content-type':'application/json'}
    })
}
