import request from '../../request'
//
export function maintainList(data) {
    return request({
        url: '/maintains/page',
        method: 'post',
        data
    })
}
//
export function addMaintain(data) {
    return request({
        url: '/maintains/add',
        method: 'post',
        data
    })
}
//
export function updateMaintain(data) {
    return request({
        url: '/maintains/update',
        method: 'post',
        data
    })
}
//
export function deleteMaintain(data) {
    return request({
        url: '/maintains/delete',
        method: 'post',
        data
    })
}
//
export function searchMaintain(data) {
    return request({
        url: '/maintains/fuzzyQuery',
        method: 'post',
        data
    })
}
//
export function maintainRecord(data) {
    return request({
        url: '/maintains/execute',
        method: 'post',
        data
    })
}

