import request from '../../request'
//
export function carlist(data) {
    return request({
        url: '/cars/list',
        method: 'post',
        data
    })
}
//
export function carAdd(data) {
    return request({
        url: '/cars/add',
        method: 'post',
        data

    })
}
//
export function cardelete(data) {
    return request({
        url: '/cars/delete',
        method: 'post',
        data
    })
}
//
export function carupdate(data) {
    return request({
        url: '/cars/update',
        method: 'post',
        data
    })
}
//
export function carSearch(data) {
    return request({
        url: '/cars/query',
        method: 'post',
        data
    })
}
//
export function addBatch(data) {
    return request({
        url: '/cars/addBatch',
        method: 'post',
        data
    })
}


//
export function setPause(data) {
    return request({
        url: '/params/setPause',
        method: 'post',
        data,
        headers:{'content-type':'application/json'}
    })
}
